.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	transform: scale(1);
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
	transition: transform $cubicBezier, opacity $cubicBezier;

	&.is-hidden {
		pointer-events: none;
		transform: scale(0);
		opacity: 0;
	}
}
.close-button {
	position: absolute;
	top: 30px;
	right: 25px;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	margin: 0;
	background-color: transparent;
	border: none;
	//   width: 30px;
	//   height: 30px;
	//   border-radius: 50%;
	//   pointer-events: initial;
	cursor: pointer;

	//   &:hover,
	//   &:focus {
	//     fill: $accentColor;
	//   }
}

.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: $accentColor;
	// overflow-x: hidden;
	// overflow-y: auto;

	@media screen and (min-width: 310px) and (max-width: 479px) {
		width: 290px;
		background-color: $accentColor;
		height: 152px;
	}
	@media screen and(min-width: 480px) and (max-width: 1199px) {
		width: 650px;
		background-color: $accentColor;
		height: 230px;
	}

	@media screen and(min-width: 1200px) {
		width: 860px;
		height: 230px;
		background-color: $accentColor;
		border: 1px solid red;
	}

	&-title {
		text-align: center;
		font-weight: bold;
		font-size: 20px;
		line-height: 1.4;
		letter-spacing: 0.03em;
		margin-top: 40px;
		margin-bottom: 30px;
	}
}
