* {
  box-sizing: border-box;
}

.close-button {
  position: absolute;
  top: 30px;
  right: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modal-add-bill {
  position: fixed;

  min-height: 100%;

  width: 290px;
  @media screen and (min-width: 768px) {
    width: 724px;
  }
  @media screen and (min-width: 1280px) {
    width: 860px;
  }
}
.add-modal__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  /* height: 100%;*/
  overflow: auto;
  background-color: #fff;
  color: #000;
  padding: 20px;
  overflow: auto;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.02em;

  color: #000000;

  @media screen and (min-width: 768px) {
    // width: 724px;
    // height: 698px;
    padding: 55px;
  }
  @media screen and (min-width: 1280px) {
    // width: 861px;
    // height: 698px;
    padding-left: 105px;
    padding-right: 105px;
  }
}
.input-wrapper._error {
  box-shadow: 0 0 15px red;
}
.add-form__error-message {
  display: none;
}
.add-form__error-message.visible {
  display: block;
  // color: red;

  box-shadow: 0 0 15px red;
}

.add-form__title {
  font-family: $main-font;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.02em;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    margin-bottom: 30px;
  }
}
.add-form__label {
  font-family: $main-font;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.02em;
  display: inline-block;
  margin-bottom: 10px;

  @media screen and (min-width: 768px) {
    width: 115px;
    margin-right: 27px;
    margin-bottom: 20px;
  }
  @media screen and (min-width: 1280px) {
    width: 130px;
    margin-right: 80px;
  }
}
.add-form__item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
  &:last-child {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    // width: 440px;

    &:last-child {
      margin-bottom: 30px;
    }
  }
}

