.input {
  width: 100%;
  // flex-grow: 1;
  // width: 440px;

  padding: 20px;
  border: 1px solid #bbbbbb;
  resize: vertical;
  @media screen and (min-width: 768px) {
    width: 458px;
  }
  @media screen and (min-width: 1280px) {
    width: 440px;
  }
}

.image-preview__add {
  margin: 0;
  display: grid;
  grid-gap: 10px;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    width: 458px;
  }
}
.choose-image__input {
  position: relative;
  opacity: 0;

  @media screen and (min-width: 768px) {
    // top: 18%;
    // left: 316px;
    border: 1px solid blue;
    // width: 100%;
    // height: 100%;
    width: 76px;
    height: 48px;
    //opacity: 0;
    // font-size: 0;
  }
}
.image-preview__item {
  position: absolute;
  width: 78px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f6fb;
  img {
    max-width: 100%;
    max-height: 100%;
  }
  @media screen and (min-width: 768px) {
    width: 84px;
    height: 50px;
  }
}

.image-button {
  font-size: 22px;
  color: #bbbbbb;
}

