.delete-advert {
  text-decoration: none;

  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  align-items: center;
  @media screen and (min-width: 768px) {
    margin-left: 120px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 175px;
  }
}
.delete-icon {
  width: 13px;
  height: 14px;
  padding-bottom: 2px;
  margin-right: 10px;
}
