    display: flex;
    align-items: center;
    justify-content: center;
    width: 266px;
    height: 60px;
  }
  @media screen and (min-width: 1280px) {
    position: absolute;
    bottom: 38%;
    left: 36%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 210px;
    height: 60px;
  }
}
