button[type='submit'] {
  background-color: #ff6b09;
  color: white;
  padding: 24px 49px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 50%;
  margin-right: 50%;
}
@media screen and (max-width: 723px) {
  button[type='submit'] {
    padding: 25px 60px;
    margin-left: 0%;
  }
}
@media screen and (max-width: 480px) {
  button[type='submit'] {
    padding: 20px 58px;
    margin-left: 0px;
  }
}

button[type='submit']:hover {
  background-color: #ac6638;
}
.row:after {
  content: '';
  display: table;
  clear: both;
}
