.form {
	position: absolute;
	top: 50%;
	left: 50%;
	margin-bottom: 0;
	transform: translate(-50%, -50%) scale(1);
	@media screen and (min-width: 480px) {
		width: 290px;
	}

	//min-height: 542px;

	@media screen and (min-width: 768px) {
		width: 650px;
	}

	outline: 1px solid black;
	margin-left: auto;
	margin-right: auto;
	padding: 60px 19px;

	@media screen and (min-width: 768px) {
		padding: 64px 104px;
	}

	background-color: #f5f6fb;
}

.autoriz-close-button {
	position: absolute;
	top: 20px;
	right: 19px;

	@media screen and (min-width: 768px) {
		top: 30px;
		right: 30px;
	}
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	margin: 0;
	background-color: transparent;
	border: none;
	cursor: pointer;
}

.autoriz-title {
	margin: 0px;
	padding: 0px;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 1.14;
	letter-spacing: 0.02em;

	@media screen and (min-width: 768px) {
		text-align: center;
	}
}
.link-contanier {
	margin-top: 20px;
	margin-bottom: 41px;
	display: inline-flex;
	align-items: center;

	@media screen and (min-width: 768px) {
		margin-top: 34px;
		margin-bottom: 42px;
	}
}
.google-link {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	padding: 17px 34px;
	background-color: $primaryWhiteColor;
	text-decoration: none;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 1.14;
	letter-spacing: 0.02em;
	color: $text-color;
}

.icon-google-symbol {
	margin-right: 10px;
}

p {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 1.14;
	letter-spacing: 0.02em;
	// margin-bottom: 20px;

	// @media screen and (min-width: 768px) {
	// 	margin-bottom: 37px;
	// }
}

