.down {
	margin-top: 20px;
	margin-bottom: 31px;

	@media screen and (min-width: 768px) {
		margin-top: 24px;
		margin-bottom: 47px;
	}
}

.form-label {
	position: absolute;
	top: 50%;
	left: 20px;
	transform: translateY(-50%);
	transition: $cubicBezier;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 1.14;
	letter-spacing: 0.02em;

	color: #bbbbbb;
}
.form-field input {
	margin: 0;
	padding-top: 20px;
	padding-bottom: 20px;
	padding-left: 25px;
	border-radius: 3px;
	border: 1px solid #bbbbbb;
	font-size: 14px;
}

.form-input:focus + .form-label,
.form-input:not(:placeholder-shown) + .form-label {
	transform: translateY(-45px);
	color: $accentColor;
}
.form-button {
	// text-align: center;
	display: flex;
	justify-content: space-between;
}
.btn-autoriz {
	width: 119px;
	height: 50px;

	@media screen and (min-width: 768px) {
		width: 210px;
		height: 60px;
	}
	background-color: $primaryWhiteColor;
	border: 1px solid #bbbbbb;
	transition: 250ms background-color cubic-bezier(0.4, 0, 0.2, 1), 250ms color cubic-bezier(0.4, 0, 0.2, 1);

	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 1.16;
	text-align: center;
	letter-spacing: 0.02em;

	&:not(:last-child) {
		@media screen and (max-width: 767px) {
			margin-right: 15px;
		}
	}
	&:hover,
	&:focus {
		background-color: $accentColor;
		color: $primaryWhiteColor;
	}
}
