/* Rules for sizing the icon. */
.material-icons.md-12 {
  font-size: 12px;
  width: 14px;
  height: 14px;
}

.material-icons.md-14 {
  font-size: 13px;
  width: 13px;
  height: 14px;
}

.material-icons.md-15 {
  font-size: 16px;
  width: 14px;
  height: 14px;
}

.material-icons.md-18 {
  font-size: 20px;
  width: 12px;
  height: 12px;
}

.devider {
  color: #e2e3e5;
