.cards-st-p:not(:first-child) {
  margin-left: 0px;
}

.cards-st-p {
  // <<<<<<< card-hover-v-1
  // position: relative;
  width: 290px;
  //   max-height: 350px;
  //   margin-bottom: 30px;
  @media screen and (min-width: $tablet-min) {
    width: 352px;
    margin-bottom: 0;
  }
  @media screen and (min-width: $desktop) {
    width: 295px;
  }

  & .backdrop-st-p {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    // opacity: 0;
    background-color: rgba(0, 0, 0, 0.7);
    visibility: hidden;

    // z-index: ;

    transform: translateY(-100%);
    transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &:hover .backdrop-st-p {
    transform: translateY(0%);
    visibility: visible;
  }
}

