%notification {
  background-color: #ccc;
  font-size: 20px;
  padding: 20px;
}

%list {
  padding: 0;
  margin: 0;
  list-style: none;
}
%container {
  width: 1350px;
  padding-left: 20px;
  padding-right: 20px;
}

%focus-hover-style {
  &:hover {
    transform: scale(1.1);
  }

  &:focus {
    outline-style: none;
    transform: scale(0.9);
    font-weight: 700;
  }
}
