* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

// Цвета
$bold-text-color: #000000;
$inactive-color: #878686;
$white-color: #ffffff;
//Анимация
$animation-timing: 0.2s ease-in;
//Шрифты
$montserrat: "Montserrat", sans-serif;
$roboto: "Roboto", sans-serif;

.card-modal-body {
	background-color: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	justify-content: center;
	// position: relative;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition: opacity var(--animation-duration) var(--timing-function);
	overflow: hidden;
	opacity: 0;
	z-index: 9999;
	pointer-events: none;
	transition: all 0.2s;
	&.is-open {
		opacity: 1;
		pointer-events: initial;
		transition: all 0.4s;
	}
}

.card-modal__overlay {
	position: relative;
	// top: 0;
	// left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
}

.card-modal__content {
	position: relative;
	// align-self: flex-start;
	display: block;
	width: 861px;
	height: 668px;
	// margin: 54px auto 0px auto;
	// margin-top: 13px;
	// margin-bottom: auto;
	margin-left: auto;
	margin-right: auto;
	// z-index: 5;
	transform: scale(0.95);
	overflow-x: hidden;
	overflow-y: auto;
	// transition: transform var(--animation-duration) var(--timing-function);
}

/** Card modal **/
.card-modal {
	position: relative;
	// top: 4%;
	width: 290px;
	max-height: 780px;
	// overflow-y: auto;
	padding: 55px 18px 60px 18px;
	margin: 0px auto 0px auto;
	// overflow-x: hidden;
	// overflow-y: auto;

	background-color: #ffffff;

	@media screen and (min-width: $tablet-min) {
		display: flex;
		width: 724px;
		min-height: 614px;
		padding-top: 57px;
	}
	@media screen and (min-width: $desktop) {
		display: flex;
		width: 861px;
		min-height: 668px;
		padding-top: 54px;
	}
}

.card-modal__close-btn {
	border: none;
	outline: none;
	position: absolute;
	top: 15px;
	right: 15px;
	cursor: pointer;
	background-color: red;
	width: 24px;
	height: 24px;

	background-color: transparent;
	border-color: transparent;

	&:hover .card-modal__icon-close,
	&:focus .card-modal__icon-close {
		color: #000;
		border: none;
		// border-color: #ffffff;
	}

	@media screen and (min-width: $tablet-min) {
		top: 30px;
		right: 30px;
	}
	@media screen and (min-width: $desktop) {
		top: 25px;
		right: 25px;
	}
}

.card-modal__icon-close {
	height: 24px;
	width: 24px;
}

