//Сброс UL
%list-throw {
	list-style: none;
	padding: 0;
}
//Сброс p, h1, h2 и т.д.
%text-throw {
	margin-top: 0;
	margin-bottom: 0;
}
// Сброс ссылки
%link-throw {
	display: block;
	text-decoration: none;
	padding-top: 15px;
	padding-bottom: 15px;
}
// Сброс кнопки
%button-throw {
	border: none;
	outline: none;
}
// Стили секции
%section-style {
	@media screen and (min-width: 320px) {
		padding-bottom: 110px;
	}
	@media screen and (min-width: $tablet-min) {
		padding-bottom: 180px;
	}
}
//Стили контейнера
%container-style {
	margin-left: auto;
	margin-right: auto;
	padding-left: 20px;
	padding-right: 20px;
	@media screen and (max-width: 479px) {
		max-width: 450px;
	}
	@media screen and (min-width: 480px) {
		width: 480px;
	}
	@media screen and (min-width: $tablet-min) {
		width: $tablet-min;
	}
	@media screen and (min-width: $desktop) {
		width: $desktop;
	}
}
// Стили UL API
%list-style {
	list-style: none;
	padding: 0;
	display: grid;
	grid-gap: 10px;
	@media screen and (min-width: 320px) {
		grid-template-columns: 1fr;
	}
	@media screen and (min-width: $tablet-min) {
		grid-template-columns: 1fr 1fr;
	}
	@media screen and (min-width: $desktop) {
		grid-template-columns: 1fr 1fr 1fr;
	}
}
// Стили картинки внутри LI
%img-style {
	display: block;
	width: 100%;
	object-fit: cover;
	object-position: center;
}
// Стили оранжевой кнопки ПК
%button-desktop-orange {
	@extend %button-throw;
	display: block;
	padding: 30px;
	text-align: center;
	color: $white-color;
	background-color: $accentColor;
	transition: $animation-timing;
	&:hover,
	&:focus {
		color: $bold-text-color;
		background-color: $white-color;
	}
}
//Стили белой кнопки ПК
%button-desktop-white {
	@extend %button-throw;
	display: block;
	padding: 30px;
	text-align: center;
	color: $bold-text-color;
	background-color: $white-color;
	transition: $animation-timing;
	&:hover,
	&:focus {
		color: $white-color;
		background-color: $accentColor;
	}
}
// Стили оранжевой кнопки планшет
%button-tablet-orange {
	@extend %button-throw;
